var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"inner-container",attrs:{"id":"vision"}},[_c('div',{staticClass:"main-text",attrs:{"id":"vision-box"}},[_c('h1',{staticClass:"box-title",attrs:{"id":"vision-title"}},[_vm._v("vision")]),_c('p',[_vm._v("\n          Across all aspects of our business, our aim is to provide value for\n          our partners through continuous growth whilst simultaneously improving\n          the efficiency of our team through effective training and development.\n          At BESIDE, we are internationally minded with deep-rooted regional\n          expertise, which is coupled by our commitment to remaining loyal to\n          the DNA of each and every brand we represent. We are dedicated to\n          building solid, trusted, and long-standing business relationships\n          through excellent operations and a versatile approach to our business.\n          We encourage ambition and open-mindedness, which is enhanced by a work\n          environment that is energetic, innovative, fun and empowering.\n        ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"main-text",attrs:{"id":"activities-box"}},[_c('h1',{staticClass:"box-title",attrs:{"id":"activities-title"}},[_vm._v("activities")]),_c('p',[_vm._v("\n          Over the past few decades, BESIDE has become the go-to partner for\n          international brands Diesel, Fred Perry, Pinko, Scotch & Soda and\n          others thanks to our well-developed know-how of the Retail and\n          Distribution sectors. This is evident through the success of our mono\n          brand stores as well as our franchising and wholesale network which\n          encompasses 11 Middle Eastern countries through our dedicated\n          showrooms in Dubai.\n        ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',