        }
      ]
    };
  },
  computed: {
    startPlay: Boolean
  },
  methods: {
    setTrue(playOnce) {
      this.startPlay = playOnce;
    },
    playAnimation(shallIPlay) {
      if (shallIPlay === true) {
        const tl = new TimelineMax();
        const speedE = document.getElementById("speed"),
          letters = document.querySelectorAll(".letters"),
          audio = document.createElement("audio");

        audio.loop = false;
        audio.src = require("@/assets/wav/woosh-01.wav");
        audio.type = "audio/wav";
        audio.volume = 0.8;

        TweenMax.set(letters, { opacity: 0 });
        TweenMax.set(".beside-tags", { opacity: 0 });
        TweenMax.set("#get-in-touch", { opacity: 0 }, { display: "none" });
        TweenMax.set("#touch-link", { opacity: 0 }, { display: "none" });
        // TweenMax.set(
        //   "#skip-intro",
        //   { opacity: 1 },
        //   { display: "block" },
        //   { zIndex: 5000 }
        // );

        tl.fromTo(
          speedE,
          1.5,
          {
            scaleX: 100,
            x: 1800,
            transformOrigin: "0% 100%",
            ease: Power4.easeOut
          },
          {
            scaleX: 1,
            x: -1800,
            transformOrigin: "0% 100%",
            ease: Power4.easeOut
          }
        );
        tl.to("#text5", 0.2, { opacity: 1 });
        tl.to("#text5", 3, { opacity: 0 });
        tl.fromTo(
          speedE,
          1.5,
          {
            scaleX: 100,
            x: 1800,
            transformOrigin: "0% 100%",
            ease: Power4.easeOut
          },
          {
            scaleX: 1,
            x: 0,
            transformOrigin: "0% 100%",
            ease: Power4.easeOut
          }
        );

        tl.to(letters, 1, { opacity: 1, ease: Power1.easeOut, delay: 0.2 });
        tl.to("#get-in-touch", 1, {
          opacity: 1,
          ease: Power1.easeOut,
          delay: 0.2
        });
        tl.to("#touch-link", 1, {
          opacity: 1,
          ease: Power1.easeOut,
          delay: 0.2
        });
        tl.to(
          "#skip-intro",
          1,
          {
            opacity: 0,
            ease: Power1.easeOut,
            delay: 0.2
          },
          7
        );

        document.querySelector("#skip-intro").onclick = function() {
          tl.progress(1, false);
        };
      } else {
        TweenMax.set("#get-in-touch", { opacity: 1 });
      }
    },
    getNews(newsTitle) {
      let i, news, tabs, checkVid, checkImg;
      news = document.getElementsByClassName("news-content");
      for (i = 0; i < news.length; i++) {
        news[i].style.display = "none";
      }
      tabs = document.getElementsByClassName("tabs");
      for (i = 0; i < tabs.length; i++) {
        tabs[i].className = tabs[i].className.replace(" active", "");
      }
      document.getElementById(newsTitle).style.display = "grid";
      // event.currentTarget.className += " active";
      document.getElementById(newsTitle.replace("-content", "")).className +=
        " active";

      checkVid = document.getElementsByClassName("news-vid");
      checkImg = document.getElementsByClassName("news-img");

      for (i = 0; i < checkVid.length; i++) {
        if (checkVid[i].currentSrc === "") {
          checkVid[i].style.display = "none";
          checkImg[i].style.display = "block";
        } else {
          checkVid[i].style.display = "block";
          checkImg[i].style.display = "none";
        }
      }
    }
  },
  mounted() {
    this.playAnimation(this.startPlay);
    const readTrigger = document.querySelectorAll(".read-more"),
      closeTrigger = document.querySelectorAll(".news-txt");

    readTrigger.forEach.call(readTrigger, el => {
      el.addEventListener("click", () => {
        const readText = el.nextSibling;
        TweenMax.fromTo(
          readText,
          0,
          { display: "none" },
          {
            display: "block",
            immediateRender: false
          }
        );
        TweenMax.fromTo(
          el,
          0,
          { display: "block" },
          {
            display: "none",
            immediateRender: false
          }
