    TweenMax.fromTo(
      "#down",
      1,
      { y: -10, ease: Bounce.easeOut, repeat: -1, yoyo: true },
      { y: 0, ease: Bounce.easeOut, repeat: -1, yoyo: true }
    );

    document.getElementById("down").addEventListener("click", () => {
      TweenMax.to(window, 1, {
